<template>
    <router-link class="flex m-6 py-8 text-lg justify-center rounded-tr-lg rounded-bl-lg uppercase" :to="url" :style="style" >{{label}}</router-link>
</template>

<script>
export default {
    name: 'LargeButton',
    props: {
        label: String,
        url: String,
        color1: String,
        color2: String
    },
    computed: {
      style () {
        return 'background: ' + this.color1 + '; background: linear-gradient(90deg, '+this.color1+' 0%, '+this.color2+' 100%)';
      }
    },
}
</script>
